/*
 * Package @spielhalle/tank-call
 * Source https://spielhalle.github.io/spielhalle/
 */
class Background extends Container {
    constructor(width, height) {
        super();
        this.gradient = this.createGradient();
        this.gradient.width = width;
        this.gradient.height = height;
        this.addChild(this.gradient);
    }
    createGradient() {
        const canv = document.createElement('canvas');
        canv.width = 100;
        canv.height = 100;
        const ctx = canv.getContext('2d');
        const gradient = ctx.createLinearGradient(0, 0, 0, 100);
        gradient.addColorStop(0, '#81C784');
        gradient.addColorStop(1, '#64B5F6');
        ctx.fillStyle = gradient;
        ctx.fillRect(0, 0, 100, 100);
        return new Sprite(Texture.from(canv));
    }
}

/*
 * Package @spielhalle/tank-call
 * Source https://spielhalle.github.io/spielhalle/
 */
class BarrelExplosion extends Graphics {
    constructor() {
        super();
        this.progress = 0;
        this.redraw();
    }
    redraw(progress = 0) {
        const sineProgress = 1 - Math.sin(Math.PI * progress);
        const explosionColor = rgb2hex([1, sineProgress, progress < 0.5 ? 0 : sineProgress]);
        const fadeProgress = progress >= BarrelExplosion.FADE_START ? 1 - (progress - BarrelExplosion.FADE_START) / (1 - BarrelExplosion.FADE_START) : 1;
        this.clear();
        this.beginFill(explosionColor, fadeProgress);
        this.moveTo(0, 0);
        this.lineTo(20, 10);
        this.arc(20, 0, 10, Math.PI * 0.5, Math.PI * 1.5, true);
        this.lineTo(0, 0);
        this.endFill();
    }
    step(delta) {
        this.progress += delta;
        if (this.progress > BarrelExplosion.DURATION) {
            this.parent.removeChild(this);
            return;
        }
        const relativeProgress = this.progress / BarrelExplosion.DURATION;
        const relativeProgressSquared = relativeProgress * relativeProgress;
        const explosionScale = relativeProgressSquared / (2.0 * (relativeProgressSquared - relativeProgress) + 1.0);
        this.scale.set(explosionScale, explosionScale);
        this.redraw(relativeProgress);
    }
}
BarrelExplosion.DURATION = 10;
BarrelExplosion.MIN_RADIUS = 0.05;
BarrelExplosion.MAX_RADIUS = 1;
BarrelExplosion.FADE_START = 0.6;

/*
 * Package @spielhalle/tank-call
 * Source https://spielhalle.github.io/spielhalle/
 */
class Landscape extends Graphics {
    constructor(hillWidth, hillHeight, hillOffset, octaves) {
        super();
        this.data = [];
        if (hillHeight <= 0) {
            throw new Error('hillHeight must larger than 0');
        }
        if (hillWidth <= 0) {
            throw new Error('hillWidth must larger than 0');
        }
        this.mHillHeight = hillHeight;
        this.mHillOffset = hillOffset;
        this.mHillWidth = hillWidth;
        this.mOctaves = octaves;
        this.regenerate();
    }
    static bezier(p0, p1, p2, p3, t) {
        return {
            x: Math.pow(1 - t, 3) * p0.x + 3 * t * Math.pow(1 - t, 2) * p1.x + 3 * Math.pow(t, 2) * (1 - t) * p2.x + Math.pow(t, 3) * p3.x,
            y: Math.pow(1 - t, 3) * p0.y + 3 * t * Math.pow(1 - t, 2) * p1.y + 3 * Math.pow(t, 2) * (1 - t) * p2.y + Math.pow(t, 3) * p3.y,
        };
    }
    static generate(width, ocataves = 6) {
        if (width % 1 !== 0) {
            throw new Error('Only integers as width allowed');
        }
        if (ocataves % 1 !== 0) {
            throw new Error('Only integers as octaves allowed');
        }
        const res = [];
        const stepWidth = Math.ceil(width / ocataves);
        for (let i = 0; i <= ocataves; i++) {
            res[i * stepWidth] = Math.random();
            if (i > 0) {
                Landscape.fillBezier(res, (i - 1) * stepWidth, i * stepWidth);
            }
        }
        return res;
    }
    static fillBezier(data, idxL, idxR) {
        const dX = idxR - idxL;
        const idxC = (idxR + idxL) / 2;
        const p0 = {
            x: idxL,
            y: data[idxL],
        };
        const p3 = {
            x: idxR,
            y: data[idxR],
        };
        const p1 = {
            x: idxC,
            y: data[idxL],
        };
        const p2 = {
            x: idxC,
            y: data[idxR],
        };
        for (let i = 0; i < dX; i++) {
            data[idxL + i] = Landscape.bezier(p0, p1, p2, p3, i / dX).y;
        }
    }
    explodeAt(x, radius = 10) {
        const roundedX = Math.round(x);
        const startHeight = this.getHeight(roundedX);
        for (let i = roundedX - radius; i <= radius + roundedX; i++) {
            const dX = i - roundedX;
            this.data[i] = Math.min(this.data[i], startHeight - Math.sin(Math.acos(dX / radius)) * radius);
            if (this.data[i] <= 0) {
                this.data[i] = 1;
            }
        }
        this.redraw();
    }
    getHeight(x) {
        return this.data[Math.trunc(x)];
    }
    set hillHeight(height) {
        this.mHillHeight = height;
        this.redraw();
    }
    get hillHeight() {
        return this.mHillHeight;
    }
    set hillWidth(width) {
        this.mHillWidth = width;
        this.regenerate();
    }
    get hillWidth() {
        return this.mHillWidth;
    }
    set hillOffset(offset) {
        this.mHillOffset = offset;
        this.redraw();
    }
    get hillOffset() {
        return this.mHillOffset;
    }
    set octaves(octaves) {
        this.mOctaves = octaves;
        this.regenerate();
    }
    get octaves() {
        return this.mOctaves;
    }
    regenerate() {
        this.sourceData = Landscape.generate(this.mHillWidth, this.mOctaves);
        for (let x = 0; x < this.sourceData.length; x++) {
            this.data[x] = this.mHillOffset + this.sourceData[x] * this.mHillHeight;
        }
        this.redraw();
    }
    redraw() {
        this.clear();
        this.beginFill(0x8b4513);
        this.lineStyle(2, 0x7cfc00, 1);
        this.moveTo(0, 0);
        for (let i = 0; i < this.data.length; i++) {
            if (this.data[i]) {
                this.lineTo(i, this.data[i]);
            }
        }
        this.lineTo(this.hillWidth, 0);
        this.lineTo(0, 0);
        this.endFill();
    }
}

/*
 * Package @spielhalle/tank-call
 * Source https://spielhalle.github.io/spielhalle/
 */
class Projectile extends Graphics {
    constructor() {
        super();
        this.lastX = 0;
        this.lastY = 0;
        this.velocity = new Point();
        this.projectileDestroyed = false;
        this.beginFill(0xff0000);
        this.moveTo(-4, 2);
        this.lineTo(2, 2);
        this.lineTo(4, 0);
        this.lineTo(2, -2);
        this.lineTo(-4, -2);
        this.lineTo(-4, 2);
        this.endFill();
    }
    step(delta) {
        this.velocity.x = Math.min(Projectile.TERMINAL_VELOCITY, this.velocity.x + Projectile.GRAVITY.x * delta);
        this.velocity.y = Math.min(Projectile.TERMINAL_VELOCITY, this.velocity.y + Projectile.GRAVITY.y * delta);
        //const totalVelocity:number=this.velocity.magnitude();
        this.velocity.multiplyScalar(0.95 * delta);
        this.lastX = this.x;
        this.lastY = this.y;
        this.x = this.x + delta * this.velocity.x;
        this.y = this.y + delta * this.velocity.y;
        if (this.velocity.x === 0) {
            this.rotation = this.velocity.y >= 0 ? 0 : Math.PI;
        }
        else {
            this.rotation = Math.atan(this.velocity.y / this.velocity.x);
        }
    }
}
Projectile.GRAVITY = new Point(0, -0.05);
Projectile.TERMINAL_VELOCITY = 100;

/*
 * Package @spielhalle/tank-call
 * Source https://spielhalle.github.io/spielhalle/
 */
class Tank extends Container {
    constructor() {
        super();
        this.mTurretAngle = 0;
        this.base = new Graphics();
        this.turret = new Graphics();
        this.addChild(this.base);
        this.addChild(this.turret);
        this.redraw();
    }
    set turretAngle(angle) {
        this.mTurretAngle = angle;
        this.redraw();
    }
    get turretAngle() {
        return this.mTurretAngle;
    }
    redraw() {
        this.base.clear();
        this.turret.clear();
        this.base.beginFill(0xff3300);
        this.base.moveTo(-20, 0);
        this.base.arc(0, 0, 20, 0, Math.PI);
        this.base.endFill();
        this.turret.lineStyle(3, 0x0033ff);
        this.turret.moveTo(Tank.TURRET_OFFSET.x, Tank.TURRET_OFFSET.y);
        this.turret.lineTo(Tank.BARREL_LENGTH * Math.cos(this.turretAngle) + Tank.TURRET_OFFSET.x, Tank.BARREL_LENGTH * Math.sin(this.turretAngle) + Tank.TURRET_OFFSET.y);
    }
}
Tank.TURRET_OFFSET = new Point(0, 10);
Tank.BARREL_LENGTH = 20;

