class TargetNumber extends Container {
    constructor(num) {
        super();
        this.mNum = 0;
        this.textStyle = new TextStyle({
            fill: '#0000FF',
            fontFamily: 'Arial',
            fontSize: 20,
            fontWeight: 'bold',
            stroke: '#4a1850',
            strokeThickness: 1,
        }); /*
        private textStyle = new TextStyle({
            fontFamily: 'Arial',
            fontSize: 36,
            fontStyle: 'italic',
            fontWeight: 'bold',
            fill: ['#ffffff', '#00ff99'], // gradient
            stroke: '#4a1850',
            strokeThickness: 5,
            dropShadow: true,
            dropShadowColor: '#000000',
            dropShadowBlur: 4,
            dropShadowAngle: Math.PI / 6,
            dropShadowDistance: 6,
            wordWrap: true,
            wordWrapWidth: 440
        });*/
        this.mNum = num;
        this.text = new Text(`${num}`, this.textStyle);
        this.addChild(this.text);
        this.centerText();
    }
    centerText() {
        this.pivot.y = this.height;
        this.scale.y = -1;
        this.text.x = this.text.width / -2;
    }
    set num(num) {
        this.mNum = num;
        this.text.text = `${num}`;
        this.centerText();
    }
    get num() {
        return this.mNum;
    }
}

/*
 * Package @spielhalle/tank-call
 * Source https://spielhalle.github.io/spielhalle/
 */
class TankGame extends Container {
    constructor(width, height) {
        super();
        this.callNumber = '';
        this.projectileContainer = new Container();
        this.numberContainer = new Container();
        this.animatedObjects = new Container();
        this.callTextStyle = new TextStyle({
            dropShadow: true,
            dropShadowAngle: Math.PI / 6,
            dropShadowBlur: 4,
            dropShadowColor: '#000000',
            dropShadowDistance: 6,
            fill: ['#ffffff', '#00ff99'],
            fontFamily: 'Arial',
            fontSize: 36,
            fontStyle: 'italic',
            fontWeight: 'bold',
            stroke: '#4a1850',
            strokeThickness: 5,
        });
        this.power = 5;
        this.powerTextStyle = new TextStyle({
            fill: '#FFFFFF',
            fontFamily: 'Arial',
            fontSize: 40,
            fontWeight: 'bold',
            stroke: '#000000',
            strokeThickness: 2,
        });
        this.gameHeight = height;
        this.gameWidth = width;
        this.init();
    }
    addPower(p) {
        this.power = Math.max(0, this.power + p);
        this.updatePowerText();
    }
    init() {
        if (this.initiated) {
            throw new Error('Already initiated');
        }
        this.powerText = new Text('Power: 0', this.powerTextStyle);
        this.powerText.scale.y = -1;
        this.callText = new Text('..', this.callTextStyle);
        this.callText.scale.y = -1;
        this.callText.position.x = this.width / 2;
        // this.callText.position.y = this.height - 60;
        // Invert because double inverted... bs
        this.interactive = true;
        this.on('pointertap', (tap) => {
            this.spawnProjectile();
        });
        this.on('pointermove', (tap) => {
            const p = tap.data.getLocalPosition(this);
            const dX = p.x - this.tank.x;
            const dY = p.y - this.tank.y;
            let rot = Math.atan2(dY, dX);
            rot = rot - this.tank.rotation;
            this.tank.turretAngle = rot;
        });
        this.background = new Background(this.gameWidth, this.gameHeight);
        this.background.x = 0;
        this.background.y = 0;
        this.addChild(this.background);
        this.landscape = new Landscape(this.gameWidth, this.gameHeight * 0.6, 20, Math.floor(this.gameWidth / 100));
        this.addChild(this.landscape);
        this.tank = new Tank();
        this.addChild(this.projectileContainer);
        this.addChild(this.tank);
        this.addChild(this.numberContainer);
        this.randomizeTankPosition();
        this.addChild(this.powerText);
        this.addChild(this.callText);
        this.updatePowerText();
        this.updateCallText();
        this.initiated = true;
        for (let i = 0; i < 9; i++) {
            this.spawnTargetNumber(i);
        }
        this.addChild(this.animatedObjects);
        const ticker = Ticker.shared;
        ticker.add((deltaT) => {
            ///console.log(deltaT / (settings.TARGET_FPMS || 1))
            for (let i = this.animatedObjects.children.length - 1; i >= 0; i--) {
                const animObj = this.animatedObjects.getChildAt(i);
                animObj.step(deltaT);
            }
            for (let deltaSubStep = 1; deltaSubStep <= TankGame.DELTA_SUB_STEPS; deltaSubStep++) {
                const deltaS = (deltaT / TankGame.DELTA_SUB_STEPS) * deltaSubStep;
                for (let i = this.projectileContainer.children.length - 1; i >= 0; i--) {
                    const pr = this.projectileContainer.getChildAt(i);
                    if (pr.destroyed || pr.projectileDestroyed) {
                        break;
                    }
                    pr.step(deltaS);
                    this.handleProjectileStep(pr);
                }
            }
        });
    }
    handleProjectileStep(pr) {
        if (pr.projectileDestroyed) {
            return;
        }
        else if (pr.y < this.landscape.getHeight(pr.x)) {
            pr.projectileDestroyed = true;
            this.landscape.explodeAt(pr.x);
            this.projectileContainer.removeChild(pr).destroy();
            this.gravitateObjects();
        }
        else if (pr.x < -20 || pr.x + 20 > this.width) {
            pr.projectileDestroyed = true;
            this.projectileContainer.removeChild(pr).destroy();
        }
        else if (pr.y < -20) {
            pr.projectileDestroyed = true;
            this.projectileContainer.removeChild(pr).destroy();
        }
        else {
            for (let n = 0; n < this.numberContainer.children.length; n++) {
                const num = this.numberContainer.getChildAt(n);
                const ba = num.getLocalBounds();
                if (this.lineIntersect(pr, { x: pr.lastX, y: pr.lastY }, { x: num.x + ba.left, y: num.y + ba.top }, { x: num.x + ba.left, y: num.y + ba.bottom })) {
                    pr.projectileDestroyed = true;
                    this.projectileContainer.removeChild(pr).destroy();
                    console.log('hit', num.num);
                    this.callNumber += num.num;
                    this.updateCallText();
                    this.relocateNumber(num);
                    break;
                }
                else if (this.lineIntersect(pr, { x: pr.lastX, y: pr.lastY }, { x: num.x + ba.left, y: num.y + ba.top }, { x: num.x + ba.right, y: num.y + ba.top })) {
                    pr.projectileDestroyed = true;
                    this.projectileContainer.removeChild(pr).destroy();
                    console.log('hit', num.num);
                    this.callNumber += num.num;
                    this.updateCallText();
                    this.relocateNumber(num);
                    break;
                }
                else if (this.lineIntersect(pr, { x: pr.lastX, y: pr.lastY }, { x: num.x + ba.right, y: num.y + ba.top }, { x: num.x + ba.right, y: num.y + ba.bottom })) {
                    pr.projectileDestroyed = true;
                    this.projectileContainer.removeChild(pr).destroy();
                    console.log('hit', num.num);
                    this.callNumber += num.num;
                    this.updateCallText();
                    this.relocateNumber(num);
                    break;
                }
                else if (this.lineIntersect(pr, { x: pr.lastX, y: pr.lastY }, { x: num.x + ba.left, y: num.y + ba.bottom }, { x: num.x + ba.right, y: num.y + ba.bottom })) {
                    pr.projectileDestroyed = true;
                    this.projectileContainer.removeChild(pr).destroy();
                    console.log('hit', num.num);
                    this.callNumber += num.num;
                    this.updateCallText();
                    this.relocateNumber(num);
                    break;
                }
            }
        }
    }
    gravitateObjects() {
        this.tank.y = this.landscape.getHeight(this.tank.x);
        this.levelTank();
        for (let n = 0; n < this.numberContainer.children.length; n++) {
            const num = this.numberContainer.getChildAt(n);
            num.y = this.landscape.getHeight(num.x);
        }
    }
    resetGame() {
        this.callNumber = '';
        this.updateCallText();
        for (let n = this.projectileContainer.children.length - 1; n >= 0; n--) {
            this.projectileContainer.removeChildAt(n).destroy();
        }
        this.landscape.regenerate();
        this.randomizeTankPosition();
        for (let n = 0; n < this.numberContainer.children.length; n++) {
            const num = this.numberContainer.getChildAt(n);
            this.relocateNumber(num);
        }
    }
    relocateNumber(d) {
        let t = false;
        do {
            t = false;
            d.x = Math.round(Math.random() * this.gameWidth);
            for (let i = 0; i < this.numberContainer.children.length; i++) {
                if (this.numberContainer.getChildAt(i) === d) {
                    continue;
                }
                else if (Math.abs(d.x - this.numberContainer.getChildAt(i).x) < 15) {
                    t = true;
                }
            }
        } while (Math.abs(this.tank.x - d.x) < 40 || t);
        d.y = this.landscape.getHeight(d.x);
    }
    lineIntersect(p0, p1, p2, p3) {
        const s1X = p1.x - p0.x;
        const s1Y = p1.y - p0.y;
        const s2X = p3.x - p2.x;
        const s2Y = p3.y - p2.y;
        const s = (-s1Y * (p0.x - p2.x) + s1X * (p0.y - p2.y)) / (-s2X * s1Y + s1X * s2Y);
        const t = (s2X * (p0.y - p2.y) - s2Y * (p0.x - p2.x)) / (-s2X * s1Y + s1X * s2Y);
        return s >= 0 && s <= 1 && t >= 0 && t <= 1;
    }
    updatePowerText() {
        this.powerText.text = `Power: ${this.power}`;
        this.powerText.pivot.y = this.powerText.height;
    }
    updateCallText() {
        this.callText.text = this.callNumber === '' ? '...' : this.callNumber;
        this.callText.pivot.y = this.callText.height;
        this.callText.position.x = (this.width - this.callText.width) / 2;
        this.callText.position.y = this.height - 150;
    }
    spawnProjectile() {
        const absoluteBarrelAngle = this.tank.rotation + this.tank.turretAngle;
        const pr = new Projectile();
        const barrelStart = this.toLocal(new Point(0, 10), this.tank);
        // Place at Barrel end
        barrelStart.x += Tank.BARREL_LENGTH * Math.cos(absoluteBarrelAngle);
        barrelStart.y += Tank.BARREL_LENGTH * Math.sin(absoluteBarrelAngle);
        pr.x = barrelStart.x;
        pr.y = barrelStart.y;
        pr.lastX = this.tank.x;
        pr.lastY = this.tank.y;
        pr.velocity.y = Math.sin(absoluteBarrelAngle) * this.power;
        pr.velocity.x = Math.cos(absoluteBarrelAngle) * this.power;
        console.log(pr.velocity);
        this.projectileContainer.addChild(pr);
        // Spawn explosion
        const exp = new BarrelExplosion();
        exp.rotation = absoluteBarrelAngle;
        exp.position.set(pr.x, pr.y);
        this.animatedObjects.addChild(exp);
    }
    spawnTargetNumber(num = 0) {
        const target = new TargetNumber(num);
        this.relocateNumber(target);
        this.numberContainer.addChild(target);
        return target;
    }
    randomizeTankPosition() {
        // RANDOMIZES THE COORDINATES
        this.tank.x = Math.round(Math.random() * this.gameWidth);
        this.tank.y = this.landscape.getHeight(this.tank.x);
        this.levelTank();
    }
    levelTank() {
        // ALIGNS THE TANK WITH THE FLOOR
        const angleSmooth = 5;
        const pL = new Point();
        const pR = new Point();
        pL.x = Math.max(0, this.tank.x - angleSmooth);
        pR.x = Math.min(this.gameWidth - 1, this.tank.x + angleSmooth);
        pL.y = this.landscape.getHeight(pL.x);
        pR.y = this.landscape.getHeight(pR.x);
        this.tank.rotation = Math.tanh((pR.y - pL.y) / (pR.x - pL.x));
    }
    increaseTankAngle(p) {
        this.tank.turretAngle += p;
    }
}
TankGame.DELTA_SUB_STEPS = 10;

